import { SET_SCENE, SETSELECTIMGINDEX } from '../actions/scene'

const initialState = {
    _current_scene: 'IndexScene',
    _selectImgIndex: 0
};

export default function quest(state = initialState, action) {
    console.log('reducer scene action call:' + action.type);
    switch(action.type) {
        case SET_SCENE:
            console.log('SET_SCENE:' + action._change_scene);
            return Object.assign({}, state, {
                _current_scene: action._change_scene
            });
        case SETSELECTIMGINDEX:
            console.log('SETSELECTIMGINDEX:' + action._selectImgIndex);
            return Object.assign({}, state, {
                _selectImgIndex: action._selectImgIndex
            });
        default:
            return state;
    }
}