import React, { useEffect } from 'react';
import queryString from 'query-string';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';

import PortraitMaster from './../components/PortraitMaster'
import CameraCapture from './../components/camera/CameraCapture'
import CameraCaptureSelect from './../components/camera/CameraCaptureSelect'
import PortraitMasterGridCaptureImageList from '../components/PortraitMasterGridCaptureImageList'
import PortraitMasterFullScreenCaptureList from './../components/PortraitMasterFullScreenCaptureList'
import SceneNavigation from './SceneNavigation'

const useStyles = makeStyles((theme) => ({
  root: {
      width: "100%",
      height: "100%"
  },
}));


export const IndexScene = ({ _current_scene, set_scene, location }) => {
  console.log('Index_scene component start');
  const classes = useStyles();
  const [_rewardState, setRewardState] = React.useState(false);
  const [_rewardCategory, setRewardCategory] = React.useState('');
  const [_rewardExp, setRewardExp] = React.useState(0);

  useEffect(() => {
    const query = queryString.parse(location.search);
    console.log(query);
    setRewardState((query.Complete == "true"));
    setRewardCategory(query.reward_category);
    setRewardExp(query.reward_exp);
  },[location]);

  const getActiveScene = () => {
    switch(_current_scene) {
      case 'CameraCapture': {
        return (<CameraCapture />);
      }
      case 'CameraCaptureSelect': {
        return (<CameraCaptureSelect />);
      }
      case 'GridCaptureList': {
        return (<PortraitMasterGridCaptureImageList  />);
      }
      case 'FullScreenCaptureList': {
        return (<PortraitMasterFullScreenCaptureList enableSubmit={false} />);
      }
      case 'SubmitCaptureList': {
        return (<PortraitMasterFullScreenCaptureList enableSubmit={true} />);
      }
      case 'PortraitMaster':
      default: {
        return (<PortraitMaster rewardState={_rewardState} rewardCategory={_rewardCategory} rewardExp={_rewardExp} />);
      }
    }
  }

  return (
    <div className={classes.root}>
      {getActiveScene()}
    </div>
  );
}

const mapStateToProps = (state) => ({
  _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(IndexScene);