import React, { useEffect, useState }  from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';
import { DeleteCanvasData } from '../actions/camera'
import { makeStyles } from '@material-ui/core/styles';
import { SetIframeFullScreen, SetIframeFinish } from './util/SendMessage';
import AppBar_Quest from './AppBar_Quest';
import PortraitMasterCaptureImageControlBar from './PortraitMasterCaptureImageControlBar'
import DefaultConfirmDialog from './DefaultConfirmDialog'
import './PortraitMaster.css'
import { playEnableClickSound } from './util/SoundPlay'

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
        background: '#000000', 
    },
    app_root: {
        position: "fixed",
        left: "0%",
        top: "0%",
        width: '100%',
        height: '8%',
        //backgroundColor: "green"
    },
    body: {
        position: "fixed",
        left: "0%",
        top: "8%",
        width: '100%',
        height: '92%',
        backgroundColor: "black"
    },
    select_text: {
        color: 'white'
    },
    ImgList: {
        overflowY: "auto",
        background: '#000000', 
    },
    bottomBar: {
        position: "fixed",
        height: '50px',
        bottom: "0px",
        width: "100%",
        backgroundColor: '#000000AA',
    },
    upload_button: {
        position: "absolute",
        top: "5px",
        left: "20px",
        height: "30px",
    },
    delete_button: {
        position: "absolute",
        top: "5px",
        right: "20px",
        height: "30px",
    },
}));

var _items = [];
export const PortraitMasterGridCaptureImageList = ({_canvasData, set_scene, DeleteCanvasData}) => {
    console.log('PortraitMasterGridCaptureImageList component start');
    const classes = useStyles();
    const [itemCount, setItemCount] = useState(0);
    const [_deleteImageDialog, setDeleteImageDialog] = React.useState(false);

    useEffect(() => {
        SetIframeFullScreen(true);
        return () => {
            SetIframeFullScreen(false);
        }
      }, []);

    const toggleSelectImage = (index) => {
        console.log("toggleSelectImage:" + index);
        var deleteState = false;
        for (var i = 0; i < _items.length; i++) {
            if (_items[i] == index) {
                _items.splice(i, 1); 
                deleteState = true;
                break;
            }
        }
        if (false == deleteState) {
            _items.push(index);
        }
        setItemCount(_items.length);
        return !deleteState;
    }

    const getSelectState = (index) => {
        for (var i = 0; i < _items.length; i++) {
            if (_items[i] == index) {
                return true;
            }
        }
        return false;
    }

    const onClickUploadImage = () => {
        console.log("onClickUploadImage call");

        var selectIndex = -1;
        for (var i = 0; i < _items.length; i++) {
            var selectState = getSelectState(_items[i]);
            if (selectState == true) {
                selectIndex = _items[i];
                break;
            }
        }

        if (-1 != selectIndex) {
            if (undefined != window.HybridApp) {
                window.HybridApp.DoShareImage(selectIndex, 'level up hero 인물사진 퀘스트');
            } else {
                //set_scene('PortraitMaster');
            }
        }
        playEnableClickSound();
    }
    
    const onClickDeleteImage = () => {
        console.log("onClickDeleteImage call");
        setDeleteImageDialog(true);
        playEnableClickSound();
    }

    const onDeleteImageDialogClose = () => {
        console.log("onDeleteImageDialogClose call");
        setDeleteImageDialog(false);
        playEnableClickSound();
    }
    
    const onDeleteImageOK = () => {
        console.log("onClickDeleteImage call");
        setDeleteImageDialog(false);
        var deleteIndexList = [];
        for (var i = 0; i < _items.length; i++) {
            var selectState = getSelectState(_items[i]);
            if (selectState == true) {
                deleteIndexList.push(_items[i]);
            }
        }
        
        var imageCount = getImageCount();
        var isRemoveAll = imageCount == deleteIndexList.length;
        console.log('======isRemoveAll:' + isRemoveAll + ', ' + imageCount + '/' + deleteIndexList.length)
        _items = [];
        setItemCount(0);

        deleteIndexList.sort(function(a, b) {
            return b - a;
        });

        //console.log('===========delete index', deleteIndexList);
        
        for (var i = 0; i < deleteIndexList.length; i++) {
            console.log('select item DeleteCanvasData call index:' + deleteIndexList[i]);
            if (undefined != window.HybridApp) {
                window.HybridApp.DeleteImageDataFromDirectory(deleteIndexList[i]);
            }
            else {
                DeleteCanvasData(deleteIndexList[i]);
            }
        }
        if (isRemoveAll == true) {
            set_scene('PortraitMaster');
        }
        playEnableClickSound();
    }

    const onImageClick = (index) => {
        console.log('onImageClick index:' + index);
        toggleSelectImage(index);
        playEnableClickSound();
    }

    const getImageNode = (index, imgSrc) => {
        var className = 'gridView-play_quest_thumb_img';
        var isSelected = getSelectState(index);
        if (true == isSelected) {
            className = 'gridView-play_quest_thumb_is_selected_img';
        }
        /// overlayElement.src = 'img/image-checked-icon.png';
        return (
            <li onClick={() => onImageClick(index)}>
                <div className='gridView-play_quest_thumb'>
                    <img className={className} src={imgSrc} alt="" />
                    {isSelected == true ? <img className='gridView-play_quest_thumb-check' src= 'img/image-checked-icon.png' alt="" /> : undefined}
                </div>
                {/* {isSelected == true ? <div className="gridView-play_quest_thumb_check">
                    <img src= 'img/image-checked-icon.png' alt="" />
                </div> : undefined} */}
                {/* <span className="gridView-play_quest_label">{index} + 이미지</span> */}
            </li>
        );
    }

    const getImageCount = () => {
        if (undefined != window.HybridApp) {
            return window.HybridApp.GetDirectoryFileCount();
        }
        else {
            return _canvasData.length;
        }
    }

    const getImageList = () => {
        var contents = [];

        if (undefined != window.HybridApp) {
            var imgCount = window.HybridApp.GetDirectoryFileCount();
            for (var i = imgCount-1; i >= 0; i--) {
                var imgText = window.HybridApp.GetImageDataFromDirectory(i);
                contents.push(getImageNode(i, imgText));
            }
        }
        else {
            for (var i = _canvasData.length-1; i >= 0; i--) {
                contents.push(getImageNode(i, _canvasData[i]));
            }
        }
        return contents;
    }

    let selectText;

    if (itemCount != 0) {
        selectText = <div className={classes.select_text}><center>{itemCount}개 선택됨</center></div>;
    } else {
        selectText = <div className={classes.select_text}><center>선택 되지 않음</center></div>;
    }
    
    let appbarTitle = '내 사진 편집';

    return (
        <div className={classes.root}>
            <div className={classes.app_root}>
                <AppBar_Quest title={appbarTitle} />
            </div>
            <div className={classes.body}>
                {selectText}
                {/* <div id="imagelist" className={classes.ImgList} /> */}
                <div className='gridView-filter-content'>
                    <ul className="gridView-play_quest">
                        {getImageList()}
                    </ul>
                </div>
            </div>
            <DefaultConfirmDialog title='사진을 삭제 하시겠습니까?' onOKClick={onDeleteImageOK} onCloseClick={onDeleteImageDialogClose} openDialog={_deleteImageDialog} />
            <PortraitMasterCaptureImageControlBar itemCount={itemCount} onClickUploadImage={onClickUploadImage} onClickDeleteImage={onClickDeleteImage} />
        </div >
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene,
    _canvasData: state.camera._canvasData
});
const mapDispatchToProps = { set_scene, DeleteCanvasData };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterGridCaptureImageList);