import React from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
        //background: '#00FF0088', 
    },
    ContentsTitle: {
        position: 'relative',
        textAlign: 'left',
        fontSize : '20px',
        color: 'black',
        left : '4%',
        width: '100%',
        height: '25px'
    },
    ContentsBody: {
        position: 'relative',
        left: '4%',
        width: '92%',
        height: '70px',
        backgroundColor: 'gray'
    },
    ContentsText: {
        position: 'absolute',
        textAlign: 'left',
        left : '0px',
        width: '100%',
        color: 'white',
        fontSize : '17px',
    },
}));

export const PortraitMasterContentsFeedback = (props) => {
    console.log('PortraitMasterContentsFeedback component start');
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <div className={classes.ContentsTitle}>
            마스터 코멘트
            </div>
            <div className={classes.ContentsBody}>
                <span className={classes.ContentsText}>
                    인물의 느낌을 잘 살리셨습니다.<br /> 아주 멋진 사진입니다!
            </span>
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterContentsFeedback);