import { CLOSECAMERA, ADDCANVASDATA, DELETECANVASDATA } from '../actions/camera'

const initialState = {
    _closeCamera: '',
    _canvasData:[]
};

export default function camera(state = initialState, action) {
    console.log('reducer scene action call:' + action.type);
    switch (action.type) {
        case CLOSECAMERA:
            console.log('CLOSECAMERA:' + action._closeCamera);
            return Object.assign({}, state, {
                _closeCamera: action._closeCamera
            });
        case ADDCANVASDATA:
            console.log('ADDCANVASDATA:' + action._addCanvasData);
            return {
                ...state,
                _canvasData: [...state._canvasData, action._addCanvasData]
            }
        case DELETECANVASDATA:
            console.log('DELETECANVASDATA:' + action._deleteCanvasDataIndex);
            return {
                ...state,
                _canvasData: [
                    ...state._canvasData.slice(0, action._deleteCanvasDataIndex),
                    ...state._canvasData.slice(action._deleteCanvasDataIndex + 1)]
            }
        default:
            return state;
    }
}