export function SetIframeFinish() {
    SendMessage("onSuccess", "*");
}

export function GetIframeReward() {
    SendMessage("onGetReward");
}

export function SetIframeFullScreen(fullScreen) {
    //console.log('=========SetIframeFullScreen:' + fullScreen)
    if (fullScreen == false) {
        SendMessage("onFitScreen");
    }
    else {
        SendMessage("onFullScreen");
    }
}

function SendMessage(text) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.parent) {
        return;
    }
    console.log('SendMessage parent:' + text);
    window.parent.postMessage(text, "*");
}

export function SetLandscapeMode(fullScreen) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.HybridApp) {
        return;
    }
    window.HybridApp.SetLandscapeMode(fullScreen);
}

export function EnableScreenMirror() {
    if (undefined == window) {
        return;
    }
    if (undefined == window.HybridApp) {
        return;
    }
    window.HybridApp.EnablingWiFiDisplay();
}

