import React, { useEffect } from 'react';

import { connect } from 'react-redux';
import { set_scene } from '../../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  ContentsBody: {
    position: "absolute",
    width: "100%",
    left: "0px",
    top: "0px",
    bottom: "50px",
  },
  ImgList: {
    display: "flex",
    overflowX: "auto",
  },
  BottomBar1: {
    position: "fixed",
    width: "40%",
    Height: "50px",
    left: "5%",
    bottom: "0px",
  },
  BottomBar2: {
    position: "fixed",
    width: "40%",
    left: "55%",
    Height: "50px",
    bottom: "0px",
  }
}));


export const CameraCaptureSelect = ({ _current_scene, set_scene }) => {
  console.log('Index_scene component start');
  const classes = useStyles();

  useEffect(() => {
    onUploadImageList();
  });

  const imgUpload = (file) => {
    console.log("imgUpload:");
    console.log(file);
    var fr = new FileReader();
    fr.onload = function (event) {
      var src = event.target.result;
      var img = new Image();
      img.onload = function () {
        AddImage(fr.result);
      };
      img.src = src;
    };
    fr.readAsDataURL(file);
  }

  const onAddImageChange = (imageInput) => {
    console.log("onAddImageChange");
    if (imageInput.target.files[0]) {
      imgUpload(imageInput.target.files[0]);
    }
  }

  const onAddImageSelect = () => {
    console.log("onAddImageSelect");
    var fileInput = document.createElement('input');
    fileInput.type = "file";
    fileInput.accept = "image/*";
    fileInput.style.display = 'none';
    fileInput.onchange = onAddImageChange;
    document.body.appendChild(fileInput);
    fileInput.click();
  }

  const onUploadImageList = () => {
    console.log('onUploadImageList');
    if (undefined != window.HybridApp) {
      var imgCount = window.HybridApp.GetDirectoryFileCount();
      for (var i = 0; i < imgCount; i++) {
        var imgText = window.HybridApp.GetImageDataFromDirectory(i);
        AddImage(imgText);
      }
    }
  }

  const onUploadData = () => {
    console.log('onUploadData');
  }

  const AddImage = (imgsrc) => {
    var element = document.createElement('img');
    element.style.display = "block";
    element.style.width = "65%";
    element.style.height = "auto";
    element.style.margin = "5px";
    element.src = imgsrc;
    var imgList = document.getElementById('imagelist');
    imgList.appendChild(element);
  }

  return (
    <div className={classes.root}>
      <div id='imagelist' className={classes.ImgList}>
      </div>
      {/* <input type='file' accept='image/*' onChange={onAddImageChange}/> */}
      <Button onClick={onAddImageSelect} className={classes.BottomBar1} variant="contained" color="secondary">
        이미지 선택
      </Button>
      <Button onClick={onUploadData} className={classes.BottomBar2} variant="contained" color="secondary">
        제출하기
      </Button>
    </div>
  );
}

const mapStateToProps = (state) => ({
  _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(CameraCaptureSelect);