export const CLOSECAMERA = 'CLOSECAMERA';
export function CloseCamera(closeCamera) {
    return {
        type: CLOSECAMERA,
        _closeCamera: closeCamera
    };
}

export const ADDCANVASDATA = 'ADDCANVASDATA';
export function AddCanvasData(data) {
    return {
        type: ADDCANVASDATA,
        _addCanvasData: data
    };
}

export const DELETECANVASDATA = 'DELETECANVASDATA';
export function DeleteCanvasData(index) {
    return {
        type: DELETECANVASDATA,
        _deleteCanvasDataIndex: index
    };
}
