import React, { useEffect, useState }  from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';
import { DeleteCanvasData } from '../actions/camera'
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import {playDisableClickSound} from './util/SoundPlay'

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
    },
    bottomBar: {
        position: "fixed",
        height: '50px',
        bottom: "0px",
        width: "100%",
        backgroundColor: '#000000AA',
    },
    upload_button: {
        position: "absolute",
        top: "5px",
        left: "20px",
        height: "30px",
    },
    delete_button: {
        position: "absolute",
        top: "5px",
        right: "20px",
        height: "30px",
    },
    BottomBarButton: {
        position: "absolute",
        height: "94%",
        top: "3%",
        background: 'linear-gradient(90deg, #cc6699 30%, #6666ff 80%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',
        width: "94%",
        left: "3%",
      },
}));

export const PortraitMasterCaptureImageControlBar = ({set_scene, itemCount, onClickUploadImage, onClickDeleteImage, onClickSubmit, DeleteCanvasData, enableSubmit}) => {
    console.log('PortraitMasterCaptureImageControlBar component start');
    console.log('enableSubmit:' + enableSubmit);
    const classes = useStyles();

    const onDisableButton = () => {
        playDisableClickSound();
    }

    const getButton = () => {
        if (true == enableSubmit) {
            return (
                <div className='quest_main_button' onClick={onClickSubmit}>
                    <span>이 사진 제출하기</span>
                </div>

                // <div className={classes.bottomBar} >
                //     <Button onClick={onClickSubmit} className={classes.BottomBarButton} variant="contained" color="secondary">이 사진 제출하기</Button>;
                // </div>
            );
        } else {
            let uploadImageButton;
            let deleteImageButton;

            if (itemCount != 0) {
                uploadImageButton = <img className={classes.upload_button} onClick={onClickUploadImage} src='img/image-select-upload-enable-icon.png' />;
                deleteImageButton = <img className={classes.delete_button} onClick={onClickDeleteImage} src='img/image-select-delete-enable-icon.png' />;
            } else {
                uploadImageButton = <img className={classes.upload_button} onClick={onDisableButton} src='img/image-select-upload-disable-icon.png' />;
                deleteImageButton = <img className={classes.delete_button} onClick={onDisableButton} src='img/image-select-delete-disable-icon.png' />;
            }
            return (
                <div className={classes.bottomBar} >
                    {uploadImageButton}
                    {deleteImageButton}
                </div>
            );
        }
    }

    return (
        <div className={classes.root}> 
            {getButton()}
        </div>
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene, DeleteCanvasData };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterCaptureImageControlBar);