import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import './PortraitMaster.css'

const useStyles = makeStyles((theme) => ({
    root: {
        position: "fixed",
        backgroundColor: '#FFFFFF88',
        width: '100%',
        height: '65px',
        bottom: '0px',
        left: '0%'
    },
    BottomBarButton: {
        position: "absolute",
        height: "80%",
        top: "8%",
        background: 'linear-gradient(90deg, #cc6699 30%, #6666ff 80%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',
        width: "94%",
        left: "3%",
    },
    BottomBarText: {
        position: "absolute",
        height: "60%",
        top: "0%",
        color: 'white',
        width: "100%",
        left: "0%",
        fontSize: "20px",
        textAlign: "center"
    },
    BottomBarSubGroup: {
        position: "absolute",
        height: "40%",
        top: "50%",
        width: "100%",
        left: "0%",
    },
    BottomBarImg : {
        position: "relative",
        //left: "0%",
        top : "3px",
        height: "80%",
        width: "auto"
    },
    BottomBarSubText: {
        position: "relative",
        height: "100%",
        //top: "0%",
        color: 'white',
        width: "100%",
        left: "5px",
        fontSize: "15px",
        textAlign: "center"
    },
}));

export const RewardButton = ({ onClick, rewardCategory, rewardExp }) => {
    const classes = useStyles(); 

    const getRewardIconPath = () => {
        //console.log('=====rewardCategory:'+ rewardCategory);
        switch (rewardCategory) {
            case 'strength': return 'https://goli8111.github.io/level-up/images/card_assets/icon_power.png';
            case 'dexterity': return 'https://goli8111.github.io/level-up/images/card_assets/icon_knowledge.png';
            case 'intelligence': return 'https://goli8111.github.io/level-up/images/card_assets/icon_skill.png';
            case 'wisdom': return 'https://goli8111.github.io/level-up/images/card_assets/icon_wisdom.png';
        }
    }

    return (
        <div className={classes.root}>
            <div onClick={onClick} className='quest_reward_main_button'>
                <span>완료하고 보상 받기</span>
            </div>
            <div onClick={onClick} className='quest_reward_button'>
                <img src={getRewardIconPath()}></img>
                <span >{rewardExp}</span>
            </div>
        </div>
    );
}

export default RewardButton;
