import React, { useEffect }  from 'react';
import './PortraitMaster.css'
import { connect } from 'react-redux';
import { set_scene, SetSelectImgIndex } from '../actions/scene';
import { makeStyles } from '@material-ui/core/styles';
import {RemoveScrollBar, fullWidthClassName} from 'react-remove-scroll-bar';
import { playEnableClickSound } from './util/SoundPlay'

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
        overflow: "hidden"
        //background: '#FFFF0088', 
    },
    ImgList: {
        display: "flex",
        overflow: "auto",
    },
}));

export const PortraitMasterSimpleCaptureList = ({set_scene, SetSelectImgIndex, _canvasData}) => {
    console.log('PortraitMasterSimpleCaptureList component start');
    const classes = useStyles();

    useEffect(() => {
        onUploadImageList();
      }, []);

    const onUploadImageList = () => {
        console.log('onUploadImageList');
        if (undefined != window.HybridApp) {
            var imgCount = window.HybridApp.GetDirectoryFileCount();
            for (var i = imgCount-1; i >= 0; i--) {
                var imgText = window.HybridApp.GetImageDataFromDirectory(i);
                AddImage(i, imgText);
            }
        }
        else {
            for (var i = _canvasData.length-1; i >= 0; i--) {
                AddImage(i, _canvasData[i]);
            }
        }
    }

    const AddImage = (index, imgsrc) => {
        var element = document.createElement('img');
        element.style.width = "90%";
        element.style.height = "auto";
        element.style.margin = "5px";
        element.src = imgsrc;
        element.onclick = function() {
            console.log("img click index:"+ index);
            SetSelectImgIndex(index);
            set_scene('FullScreenCaptureList');
            playEnableClickSound();
        };
        var imgList = document.getElementById('imagelist');
        imgList.appendChild(element);
    }

    const onClickGridCaptureList = () => {
        console.log("onClickGridCaptureList");
        set_scene('GridCaptureList');
        playEnableClickSound();
    }

    return (
        <div className={classes.root}>
            <div className='simple_capture_left_title'>내 사진</div>
            <img className='simple_capture_right_title' onClick={onClickGridCaptureList} src='img/image-modify-icon.png' />
            <div>
                <br/>
                <div id='imagelist' className="SimpleCaptureImageList" />
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene,
    _canvasData: state.camera._canvasData
});
const mapDispatchToProps = { set_scene, SetSelectImgIndex };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterSimpleCaptureList);