import React, { Component } from 'react'
import './../PortraitMaster.css';
import { connect } from 'react-redux';

import { set_scene } from '../../actions/scene'
import { AddCanvasData } from '../../actions/camera'

import { SetIframeFullScreen } from './../util/SendMessage'
import { playEnableClickSound, playDisableClickSound, playCameraShutterSound} from './../util/SoundPlay'

var _videoUserCameraFlag = false;

const btnTopBatStyle = {
    //backgroundColor: '#FF0000',
    position: "fixed",
    left: "0px",
    top: "0",
    width: "100%",
    height: "11.6%",
}

const btnBottomBarStyle = {
    //backgroundColor: '#FF0000',
    position: "fixed",
    left: "0px",
    top: "88.4%",
    height: "11.6%",
    width: "100%",
}

const rootStyle = {
    position: "fixed",
    left: "0%",
    top: "0%",
    width: "100%",
    height: "100%",
    zIndex: '-3',
    backgroundColor : 'black'
}

const videoStyle = {
    position: "fixed",
    left: "0%",
    //top: "0%",
    width: "100%",
    height: "auto",
    top: "50%",
    transform: 'translate(0%, -50%)',
    zIndex: '-2'
}

const canvasStyle = {
    position: "fixed",
    left: "0%",
    //top: "0%",
    width: "100%",
    height: "auto",
    top: "50%",
    transform: 'translate(0%, -50%)',
    zIndex: '-1'
}

const btnLeftBottomStyle = {
    position: "fixed",
    height: "5.1%",
    left: "10%",
    top: "91.5%"
}

const btnCenterBottomStyle = {
    position: "fixed",
    height: "9.1%",
    left: "40%",
    top: "89.5%"
}

const btnRightBottomStyle = {
    position: "fixed", 
    height: "5.1%",
    left: "75%",
    top: "91.5%"
}

const btnTopStyle1 = {
    position: "fixed",
    height: "5.1%",
    left: "5%",
    top: "3.5%"
}

const btnTopStyle2 = {
    position: "fixed",
    height: "5.1%",
    left: "25%",
    top: "3.5%"
}

const btnTopStyle3 = {
    position: "fixed",
    height: "5.1%",
    left: "45%",
    top: "3.5%"
}

const btnTopStyle4 = {
    position: "fixed",
    height: "2.1%",
    left: "65%",
    top: "5%"
}

const btnTopStyle5 = {
    position: "fixed",
    height: "5.1%",
    left: "85%",
    top: "3.5%"
}

// const flashEffect = {
//     position: "fixed",
//     height: "100%",
//     width: "100%",
//     left: "0%",
//     top: "0%",
//     backgroundColor : "white"
// }

class CameraCapture extends Component {

    static defaultProps = {
        showVideo: false,
        showDebugVideo: true,
        loadingText: 'Loading...please be patient...'
    }

    constructor(props) {
        super(props, CameraCapture.defaultProps)
        //this.resize = this.resize.bind(this);
        this.stopCamera = this.stopCamera.bind(this);

        this.state = {
            cameraCaptureTimerType: 0,
            cameraFlashType: 0,
            cameraFilterType: 0,
            captureRatioType: 0,
            gridView: false,
            flashEffect: false
        };
    }

    getCanvas = elem => {
        this.canvas = elem
    }

    getCanvasOverlay = elem => {
        this.canvasOverlay = elem
    }

    getVideo = elem => {
        this.video = elem;
    }

    async componentDidMount() {
        SetIframeFullScreen(true);
        try {
            await this.setupCamera();
        } catch (error) {
            throw new Error(
                'This browser does not support video capture, or this device does not have a camera'
            )
        }
    }

    componentWillUnmount() {
        SetIframeFullScreen(false);
        this.setFlashLight(false);
        this.stopCamera();
    }

    async setupCamera() {
        if (!navigator.mediaDevices || !navigator.mediaDevices.enumerateDevices) {
            throw new Error(
                'Browser API navigator.mediaDevices.getUserMedia not available'
            )
        }
        navigator.mediaDevices.enumerateDevices()
            .then(function (devices) {
                devices.forEach(function (device) {
                    if (device.kind === 'videoinput') {
                        //_videoCamneraList.push(device.deviceId);
                    }
                    console.log(device.kind + ": " + device.label + ", id = " + device.deviceId);
                });
            })
            .catch(function (err) {
                console.log(err.name + ": " + err.message);
            });

        //this.resize();
        this.playCamera();
        this.updateCamera();
    }

    async playCamera() {
        if (window.stream) {
            window.stream.getTracks().forEach(track => {
                track.stop();
            });
        }

        const video = this.video;
        //video.style.transform = 'rotatey(180deg)';

        // const videoSource = _videoCamneraList[_videoSelectIndex];
        // console.log('videoSource:' + videoSource);

        var screenWidth = 1080;//1280;//1920;//video.width;//window.innerWidth;
        var screenHeight = 1080;//720;//1080;//video.height;//window.innerHeight;

        var facingModeText = 'user';
        if (_videoUserCameraFlag == false) {
            facingModeText = 'environment';
        }
        const stream = await navigator.mediaDevices.getUserMedia({
          audio: false,
          video: {
            //deviceId: videoSource ? { exact: videoSource } : undefined,
            facingMode: facingModeText,
            frameRate: 60,
            width: {exact: screenWidth},
            height: {exact: screenHeight},
            aspectRatio: {
                exact: screenWidth / screenHeight
            }
          }
        })

        window.stream = stream;
        video.srcObject = stream;
        video.play();
    }

    stopCamera() {
        console.log('stopCamera call');
        const video = this.video;
        //console.log(video);
        if (undefined == video) {
            return;
        }
        video.pause();
        video.src = "";
        if (undefined != video.srcObject) {
            video.srcObject.getTracks()[0].stop();
            video.srcObject = undefined;
        }
        console.log("video off");
    }

    setFlashLight(state) {
        console.log('setFlashLight:'+state);
        if (undefined == window.stream) {
            return;
        }
        const track = window.stream.getVideoTracks()[0];
        console.log(track);
        if (undefined == track) {
            return;
        }

        var capabilities = track.getCapabilities();
        console.log(capabilities);
        if (undefined == capabilities) {
            return;
        }
        console.log(capabilities.torch);
        if (capabilities.torch) {
            track.applyConstraints({
                advanced: [{ torch: state }]
            }).catch(e => console.log(e));
        }
    }

    updateCamera() {
        const canvas = this.canvas;
        if (undefined == canvas) {
            return;
        }
        const canvasContext = canvas.getContext('2d')
        if (undefined == canvasContext) {
            return;
        }
        const video = this.video;
        if (undefined == video) {
            return;
        }
        //console.log('showVideo:' + this.props.showVideo);
        if (false == this.props.showVideo) {
            video.style.display = "none";
        }
        else {
            video.style.display = "block";
        }
        this.updateCameraFrame(canvasContext)
    }

    updateCameraFrame(canvasContext) {
        const video = this.video;
        const findUpdateCameraFrame = async () => {
            const canvas = this.canvas;
            if (true == this.props.showDebugVideo && undefined != canvas) {

                if (video.videoWidth != canvas.width || video.videoHeight != canvas.height) {
                    canvas.width = video.videoWidth;
                    canvas.height = video.videoHeight;
                    canvasContext.width = video.videoWidth;
                    canvasContext.height = video.videoHeight;
                }
                //console.log('resolution width:' + video.videoWidth + ", canvas width:" + canvas.width);
                //console.log('video pos x:' + canvas.style.left + ", y:" + canvas.style.top);
                //console.log(video);
                //console.log('video:' + video.left + ',' + video.top + ',' + video.width + ',' + video.height);
                canvasContext.clearRect(0, 0, canvas.width, canvas.height);
                //CanvasFPS_Update();
                canvasContext.save();

                switch (this.state.cameraFilterType) {
                    case 0: {
                        canvasContext.filter = 'none';
                    }break;
                    case 1: {
                        canvasContext.filter = 'grayscale(100%)';
                    }break;
                    case 2: {
                        canvasContext.filter = 'brightness(100%)';
                    }break;
                    case 3: {
                        canvasContext.filter = 'contrast(100%)';
                    }break;
                    case 4: {
                        canvasContext.filter = 'sepia(100%)';
                    }break;
                    case 5: {
                        canvasContext.filter = 'invert(100%)';
                    }break;
                    case 6: {
                        canvasContext.filter = 'saturate(100%)';
                    }break;
                    case 7: {
                        canvasContext.filter = 'blur(4px)';
                    }break;
                }
                //canvasContext.scale(-1, 1)
                //canvasContext.translate(-video.width, 0)
                
                canvasContext.drawImage(video, 0, 0);//, video.videoWidth, video.videoHeight)
                canvasContext.restore();

                // var fontSize = canvas.width/20;
                // canvasContext.fillStyle = 'white';
                // canvasContext.shadowColor = 'rgba(0,0,0,1)';
                // canvasContext.shadowBlur = 2;
                // canvasContext.shadowOffsetX = 3;
                // canvasContext.shadowOffsetY = 3;
                // canvasContext.font = fontSize + "px Arial";
                // canvasContext.textAlign = "center";
                // canvasContext.fillText('목표', canvas.width - fontSize*4, fontSize);
                //CanvasFPS_Render(canvasContext);
            }

            if ('start' == this.props._closeCamera) {
                this.stopCamera();
                this.props.onCloseCamera('complete');
                return;
            }
            requestAnimationFrame(findUpdateCameraFrame)
        }
        findUpdateCameraFrame()
    }

    updateCameraGrid = (viewState) => {
        const video = this.video;
        if (undefined == video) {
            return;
        }

        const canvas = this.canvasOverlay;
        if (undefined == canvas) {
            return;
        }
        const canvasContext = canvas.getContext('2d')
        if (undefined == canvasContext) {
            return;
        }
    
        if (video.videoWidth != canvas.width || video.videoHeight != canvas.height) {
            canvas.width = video.videoWidth;
            canvas.height = video.videoHeight;
            canvasContext.width = video.videoWidth;
            canvasContext.height = video.videoHeight;
        }

        canvasContext.clearRect(0, 0, canvas.width, canvas.height);

        if (true == viewState) {
            canvasContext.save();
            canvasContext.strokeStyle = 'rgb(255,0,0)';
            canvasContext.lineWidth = 5;//Math.floor(Math.random() * Math.floor(3))+1; 
            canvasContext.globalAlpha = 0.3;

            for (var x = 0; x <= canvas.width; x += canvas.width/3) {
                canvasContext.moveTo(x, 0);
                canvasContext.lineTo(x, canvas.height);
            }
            for (var y = 0; y <= canvas.height; y += canvas.height/3) {
                canvasContext.moveTo(0, y);
                canvasContext.lineTo(canvas.width, y);
            }
            canvasContext.stroke();
            canvasContext.restore();
        }
    }

    onNextCamera = () => {
        // _videoSelectIndex++;
        // if (_videoCamneraList.length <= _videoSelectIndex)
        //   _videoSelectIndex = 0;
        // console.log('camera index : ' + _videoSelectIndex);

        _videoUserCameraFlag = !_videoUserCameraFlag;
        console.log('user camera flag : ' + _videoUserCameraFlag);
        this.playCamera();
        if (true == _videoUserCameraFlag) {
            this.onFlash();
        }
        else {
            this.setFlashLight((this.state.cameraFlashType == 1) ? true : false);
        }
        playEnableClickSound();
    }

    onTimerSelect = () => {
        var currentValue = this.state.cameraCaptureTimerType;
        // currentValue++;
        // if (currentValue > 3) {
        //     currentValue = 0;
        // }
        // this.setState(() => {
        //     return {cameraCaptureTimerType: currentValue};
        //   });
        // console.log("timer:" + currentValue);
        // playEnableClickSound();
        playDisableClickSound();
    }

    onCaptureAnimation = () => {
        console.log('onCaptureAnimation call');
        if (true == this.state.flashEffect) {
            console.log('onCaptureAnimation call fail!');
            return;
        }
        this.setState({
            flashEffect: true
          });
        setTimeout(() => {
            this.setState({
                flashEffect: false
              });
        }, 800);
        playCameraShutterSound();
    }

    onCapture = () => {
        const canvas = this.canvas;
        if (undefined == canvas) {
            return;
        }

        var delayDuration = 0;
        switch (this.state.cameraCaptureTimerType) {
            case 0:
                delayDuration = 0;
                break;
            case 1:
                delayDuration = 3000;
                break;
            case 2:
                delayDuration = 5000;
                break;
            case 3:
                delayDuration = 10000;
                break;
        }
        var d = new Date();
        var dateText = d.toISOString();
        if (undefined == window.HybridApp) {
            setTimeout(() => {
                var saveImage = canvas.toDataURL("image/jpeg", 1.0);
                this.props.onAddCanvasData(saveImage);
                this.onCaptureAnimation();
            }, delayDuration);
        }
        else {
            setTimeout(() => {
                var saveImage = canvas.toDataURL("image/jpeg", 1.0);
                if (undefined != window.HybridApp) {
                    window.HybridApp.SaveAlbumJpgImage(saveImage, dateText + ".jpg");
                } else {
                    var a = document.createElement("a");
                    document.body.appendChild(a);
                    a.style = "display: none";
                    a.href = saveImage;
                    a.download = dateText + ".jpg";
                    a.click();
                    window.URL.revokeObjectURL(saveImage);
                }
                this.onCaptureAnimation();
                console.log("onCapture");
            }, delayDuration);
        }
    }

    onBack = () => {
        console.log("onBack");
        this.props.onSetScene("PortraitMaster");
        playEnableClickSound();
    }

    onFlash = () => {
        var currentValue = this.state.cameraFlashType;
        currentValue++;
        if (currentValue > 1) {
            currentValue = 0;
        }
        // 유저 카메라 모드에소는 라이트를 쓰지 않게함
        if (true == _videoUserCameraFlag) {
            currentValue = 0;
            if (undefined != window.HybridApp) {
                window.HybridApp.ShowToast('전면 카메라에서는 라이트를 사용 할 수 없습니다.');
            }
        }
        this.setState(() => {
            return {cameraFlashType: currentValue};
          });

        this.setFlashLight((currentValue == 1) ? true : false);
        console.log("flash:" + currentValue);
        playEnableClickSound();
    }
    
    onToggleGrid = () => {
        var viewState = !this.state.gridView;
        this.setState(() => {
            return {gridView: viewState};
          });
        console.log("grid view:" + viewState);
        this.updateCameraGrid(viewState);
        playEnableClickSound();
    }

    onCaptureRatio = () => {
        var currentValue = this.state.captureRatioType;
        // currentValue++;
        // if (currentValue > 1) {
        //     currentValue = 0;
        // }
        // this.setState(() => {
        //     return {captureRatioType: currentValue};
        //   });
        console.log("capture Ratio Type:" + currentValue);
        playDisableClickSound();
    }


    onMovieFilter = () => {
        var currentValue = this.state.cameraFilterType;
        currentValue++;
        if (currentValue > 7) {
            currentValue = 0;
        }
        this.setState(() => {
            return {cameraFilterType: currentValue};
          });
        console.log("camera filter:" + currentValue);
        playDisableClickSound();
    }
    
    render() {
        let timerIcon;
        switch (this.state.cameraCaptureTimerType) {
            case 0: {
                timerIcon = <img style={btnLeftBottomStyle} onClick={this.onTimerSelect.bind(this)} src='img/TimerOff.png' />;
            } break;
            case 1: {
                timerIcon = <img style={btnLeftBottomStyle} onClick={this.onTimerSelect.bind(this)} src='img/Timer3.png' />;
            } break;
            case 2: {
                timerIcon = <img style={btnLeftBottomStyle} onClick={this.onTimerSelect.bind(this)} src='img/Timer5.png' />;
                
            } break;
            case 3: {
                timerIcon = <img style={btnLeftBottomStyle} onClick={this.onTimerSelect.bind(this)} src='/mg/Timer10.png' />;
            } break;
        }

        let flashIcon;
        switch (this.state.cameraFlashType) {
            case 0: {
                flashIcon = <img style={btnTopStyle2} onClick={this.onFlash.bind(this)} src='img/FlashOff.png' />
            } break;
            case 1: {
                flashIcon = <img style={btnTopStyle2} onClick={this.onFlash.bind(this)} src='img/FlashOn.png' />
            } break;
        }
        
        let gridIcon;
        if (false == this.state.gridView) {
            gridIcon = <img style={btnTopStyle3} onClick={this.onToggleGrid.bind(this)} src='img/GridOff.png' />
        } else {
            gridIcon = <img style={btnTopStyle3} onClick={this.onToggleGrid.bind(this)} src='img/GridOn.png' />
        }

        let captureRatioIcon;
        if (0 == this.state.captureRatioType) {
            captureRatioIcon = <img style={btnTopStyle4} onClick={this.onCaptureRatio.bind(this)} src='img/CaptureRatio4_3.png' />
        } else {
            captureRatioIcon = <img style={btnTopStyle4} onClick={this.onCaptureRatio.bind(this)} src='img/CaptureRatio16_9.png' />
        }

        let filterIcon;
        if (0 == this.state.cameraFilterType) {
            filterIcon = <img style={btnTopStyle5} onClick={this.onMovieFilter.bind(this)} src='img/FilterOff.png' />
        } else {
            filterIcon = <img style={btnTopStyle5} onClick={this.onMovieFilter.bind(this)} src='img/FilterOn.png' />
        }

        return (
            <div style={rootStyle}>
                {/* <meta http-equiv="Content-Security-Policy" content="default-src * gap:; script-src * 'unsafe-inline' 'unsafe-eval'; connect-src *; img-src * data: blob: android-webview-video-poster:; style-src * 'unsafe-inline';" /> */}
                <video id='videoCamera' style={videoStyle} src="" playsInline ref={this.getVideo} />
                <canvas style={canvasStyle} ref={this.getCanvas} />
                <canvas id='canvasOverlay' style={canvasStyle} ref={this.getCanvasOverlay} />

                <div style={btnTopBatStyle}>
                    <img style={btnTopStyle1} onClick={this.onBack.bind(this)} src='img/CameraBack.png' />
                    {flashIcon}
                    {gridIcon}
                    {captureRatioIcon}
                    {filterIcon}
                    {/* <IconButton onClick={this.onBack.bind(this)}><ArrowBackIosOutlinedIcon color="primary" fontSize="large" /></IconButton>
                    <IconButton onClick={this.onFlash.bind(this)}>{flashIcon}</IconButton>
                    <IconButton onClick={this.onToggleGrid.bind(this)}><GridOnOutlinedIcon color="primary" fontSize="large" /></IconButton>
                    <IconButton onClick={this.onMovieFilter.bind(this)}><MovieFilterOutlinedIcon color="primary" fontSize="large" /></IconButton> */}
                </div>

                <div style={btnBottomBarStyle}>
                    {timerIcon}
                    <img style={btnCenterBottomStyle} onClick={this.onCapture.bind(this)} src='img/shutter_normal.png' />
                    <img style={btnRightBottomStyle} onClick={this.onNextCamera.bind(this)} src='img/FlipCamera.png' />
                </div>

                <div className={"CameraCaptureEffect " + (this.state.flashEffect ? 'CameraCaptureAnimation' : '')}>
                </div>
            </div>
        )
    }
}

let mapStateToProps = (state) => {
    return {
        _current_scene: state.scene._current_scene,
        _closeCamera: state.camera._closeCamera
    };
}

let mapDispatchToProps = (dispatch) => {
    return {
        onSetScene: (state) => dispatch(set_scene(state)),
        onAddCanvasData: (img) => dispatch(AddCanvasData(img)),
    }
}

CameraCapture = connect(mapStateToProps, mapDispatchToProps)(CameraCapture);

export default CameraCapture;
