import React, {useEffect} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';

export const DefaultConfirmDialog = ({title, onOKClick, onCloseClick, openDialog}) => {
  return (
    <div>
      <Dialog
        open={openDialog}
        onClose={onCloseClick}
        aria-labelledby="alert-dialog-title"
      >
        <DialogTitle id="alert-dialog-title" style={{textAlign:'center'}}>{title}</DialogTitle>
        <DialogActions>
          <Button onClick={onCloseClick} color="primary">
            취소
          </Button>
          <Button onClick={onOKClick} color="primary" autoFocus>
            확인
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default DefaultConfirmDialog;