import React, { useEffect, useState }  from 'react';
import { connect } from 'react-redux';
import { set_scene, SetSelectImgIndex } from '../actions/scene';
import { DeleteCanvasData } from '../actions/camera'
import { makeStyles, formatMs } from '@material-ui/core/styles';
import { SetIframeFullScreen, SetIframeFinish } from './util/SendMessage';
import AppBar_Quest from './AppBar_Quest';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { PortraitMasterCaptureImageControlBar } from './PortraitMasterCaptureImageControlBar';
import DefaultConfirmDialog from './DefaultConfirmDialog'

import { playEnableClickSound } from './util/SoundPlay'


const useStyles = makeStyles((theme) => ({
    root: {
        position: "absolute",
        left: "0%",
        top: "0%",
        width: "100%",
        height: "100%",
        backgroundColor: 'black'
    },
    app_root: {
        position: "fixed",
        left: "0%",
        top: "0%",
        width: '100%',
        height: '8%',
        //backgroundColor: "green"
    },
    body: {
        position: "fixed",
        left: "0%",
        top: "8%",
        width: '100%',
        height: '92%',
        //backgroundColor: "red"
    },
    multi_select_text: {
        color: 'white'
    },
    image_count_select_text: {
        color: 'white'
    },
    ImgList: {
        overflowY: "auto",
        backgroundColor: 'black'
    },
}));

var _items = [];
export const PortraitMasterFullScreenCaptureList = ({_canvasData, set_scene, SetSelectImgIndex, _selectImgIndex, DeleteCanvasData, enableSubmit}) => {
    console.log('PortraitMasterFullScreenCaptureList component start');
    console.log('enableSubmit:' + enableSubmit);
    const classes = useStyles();
    const [itemCount, setItemCount] = useState(0);
    const [_deleteImageDialog, setDeleteImageDialog] = React.useState(false);
    const [_submitImageDialog, setSubmitImageDialog] = React.useState(false);

    useEffect(() => {
        onUpdateImageList();
        SetIframeFullScreen(true);
        return () => {
            SetIframeFullScreen(false);
        }
      }, []);
    const onUpdateImageList = () => {
        _items = [];
        console.log('onUpdateImageList');
        if (undefined != window.HybridApp) {
            var imgCount = window.HybridApp.GetDirectoryFileCount();
            //for (var i = imgCount-1; i >= 0; i--) {
            for (var i = 0; i < imgCount; i++) {
                var imgText = window.HybridApp.GetImageDataFromDirectory(i);
                AddImage(i, imgText);
            }
        }
        else {
            //for (var i = _canvasData.length-1; i >= 0; i--) {
            for (var i = 0; i < _canvasData.length; i++) {
                AddImage(i, _canvasData[i]);
            }
        }
    }

    const AddImage = (index, imgsrc) => {
        var node = {};
        node.index = index;
        node.image = imgsrc;
        _items.push(node);
        setItemCount(_items.length);
        console.log('item count:' + _items.length);
    }

    const onClickUploadImage = () => {
        console.log("onClickUploadImage call");
        if (undefined != window.HybridApp) {
            window.HybridApp.DoShareImage(_selectImgIndex, 'level up hero 인물사진 퀘스트');
            //window.HybridApp.DoShare('level up hero', '인물사진 퀘스트', 'level up hero');
        } else {
            //set_scene('PortraitMaster');
        }
        playEnableClickSound();
    }

    const onClickDeleteImage = () => {
        console.log("onClickDeleteImage call index:" + _selectImgIndex);
        setDeleteImageDialog(true);
        playEnableClickSound();
    }

    const onSwipeSound = () => {
        console.log("onSwipeSound call:");
        playEnableClickSound();
    }

    const onSelectImage = (idx) => {
        console.log("onSelectImage call:" + idx);
        SetSelectImgIndex(idx);
        //playEnableClickSound();
    }
    
    const onSubmitImageOK = () => {
        console.log('onSubmitImageOK call');
        playEnableClickSound();
        SetIframeFinish();
        set_scene('PortraitMaster');
        setSubmitImageDialog(false);
    }

    const onSubmitImageDialogOpen = () => {
        console.log('onSubmitImageDialogOpen call');
        setSubmitImageDialog(true);
        playEnableClickSound();
    }

    const onSubmitImageDialogClose = () => {
        console.log('onSubmitImageDialogClose call');
        setSubmitImageDialog(false);
        playEnableClickSound();
    }

    const onDeleteImageOK = () => {
        console.log("onDeleteImageOK call:" + _selectImgIndex);
        setDeleteImageDialog(false);
        
        if (undefined != window.HybridApp) {
            window.HybridApp.DeleteImageDataFromDirectory(_selectImgIndex);
        }
        else {
            DeleteCanvasData(_selectImgIndex);
        }

        SetSelectImgIndex(0);
        if (_items.length <= 1) {
            set_scene('PortraitMaster');
        }
        playEnableClickSound();
    }

    const onDeleteImageDialogClose = () => {
        console.log("onDeleteImageDialogClose call");
        setDeleteImageDialog(false);
        playEnableClickSound();
    }

    const getCarousel = () => {
        console.log('_items count:' + _items.length);
        return (
        <Carousel infiniteLoop={true} onSwipeEnd={onSwipeSound} selectedItem={_selectImgIndex} showArrows={true} showStatus={false} showThumbs={false} showIndicators={false} onChange={onSelectImage}>
            {
                _items.map( node => {
                    //console.log('=============_items index:', node.index)
                return <img key={node.index} src={node.image} />
                })
            }
        </Carousel>)
    }

    let selectText;
    if (itemCount > 1) {
        selectText = <div className={classes.multi_select_text}><center>슬라이드하면 다른 사진 표시</center></div>;
    } else {
        selectText = <div className={classes.multi_select_text}></div>
    }

    let imageCountText;
    if (itemCount > 1) {
        imageCountText = <div className={classes.image_count_select_text}><center>{_selectImgIndex+1}/{itemCount}</center></div>;
    } else {
        imageCountText = <div className={classes.image_count_select_text}></div>
    }

    let appbarTitle = '사진 보기';
    if (enableSubmit == true) {
        appbarTitle = '제출하기';
    }
    console.log('itemCount renderring update');
    return (
        <div className={classes.root}>
            <div className={classes.app_root}>
                <AppBar_Quest title={appbarTitle} />
            </div>
            <div className={classes.body}>
                {selectText}
                {getCarousel()}
                {imageCountText}
            </div>
            <DefaultConfirmDialog title='사진을 삭제 하시겠습니까?' onOKClick={onDeleteImageOK} onCloseClick={onDeleteImageDialogClose} openDialog={_deleteImageDialog} />
            <PortraitMasterCaptureImageControlBar itemCount={1} onClickUploadImage={onClickUploadImage} onClickDeleteImage={onClickDeleteImage} onClickSubmit={onSubmitImageDialogOpen} enableSubmit={enableSubmit} />
            <DefaultConfirmDialog title='스탬프를 받을 때까지 다시 제출할 수 없습니다.' onOKClick={onSubmitImageOK} onCloseClick={onSubmitImageDialogClose} openDialog={_submitImageDialog} />
        </div >
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene,
    _canvasData: state.camera._canvasData,
    _selectImgIndex: state.scene._selectImgIndex
});
const mapDispatchToProps = { set_scene, SetSelectImgIndex, DeleteCanvasData };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterFullScreenCaptureList);