import React, { useEffect } from 'react';
import { useDispatch, connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import { playEnableClickSound } from './util/SoundPlay'
import './Appbar.css'

const useStyles = makeStyles(theme => ({
  root: {
    position: "absolute",
    top: "0",
    left: "0",
    width: '100%',
    height: '100%',
    zIndex: 1,
    backgroundColor: "black",
    color: "white"
  },
  bar: {
    position: "absolute",
    height: "100%",
    width: "100%",
    margin: "2%",
  },
  title: {
    position: "absolute",
    zIndex: 1,
    textAlign: "left",
    margin: "2%",
    color: "white"
  },
}));

// ArrowBackIos
export const AppBar_Quest = ({title, set_scene}) => {
  const classes = useStyles();
  
  useEffect(() => {
  });


  const onBack = () => {
    console.log('onBack set_page');
    set_scene('PortraitMaster');
    playEnableClickSound();
  };

  return (
    <div className={classes.root}>
        <div className={classes.bar}>
            <span onClick={onBack} >
              <img className='appbar_title_img_arrow' src='img/CameraBack.png' />
            </span>
            <span className='appbar_quest_title_text'>
              {title}
            </span>
        </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
});
const mapDispatchToProps = { set_scene };
export default connect(mapStateToProps, mapDispatchToProps)(AppBar_Quest);