
function playSound(fileName) {
    console.log('playSound:' + fileName);
    var audio = undefined;
    if (undefined == window.HybridApp) {
        audio = new Audio('./sound/' + fileName);
    } else {
        audio = new Audio('file:///android_asset/sound/' + fileName);
    }
    audio.play();
}

export function playEnableClickSound() {
    playSound('Common_Button_Enabled_Touch.wav');
}

export function playDisableClickSound() {
    playSound('Common_Button_Disabled_Touch.wav');
}

