import React from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';

import CameraAltIcon from '@material-ui/icons/CameraAlt';
import HomeIcon from '@material-ui/icons/Home';

const useStyles = makeStyles({
    root: {
        width: '100%',
        // height: '100%',
        bottom: 0,
        position: 'fixed',
        flexGrow: 1,
    },
});

export const SceneNavigation = ({ _current_scene, set_scene }) => {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    return (
        <BottomNavigation
            value={value}
            onChange={(event, newValue) => {
                set_scene(newValue);
                setValue(newValue);
            }}
            showLabels
            className={classes.root}
        >
            <BottomNavigationAction value="PortraitMaster" label="Home" icon={<HomeIcon />} />
            <BottomNavigationAction value="CameraCapture" label="사진찍기" icon={<CameraAltIcon />} />

        </BottomNavigation>
    );
}


const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(SceneNavigation);