export const SET_SCENE = 'SET_SCENE';
export function set_scene(change_scene) {
    return {
        type: SET_SCENE,
        _change_scene: change_scene
    };
}

export const SETSELECTIMGINDEX = 'SETSELECTIMGINDEX';
export function SetSelectImgIndex(index) {
    return {
        type: SETSELECTIMGINDEX,
        _selectImgIndex: index
    };
}
